.background-container {
  position: fixed;  /* Ensures the background stays fixed */
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;  /* Prevents interactions with the SVGs */
  z-index: -1;  /* Puts the background behind all page content */
  background-color: var(--background-color);
  padding: 4rem;
}

