
@import url('https://fonts.googleapis.com/css2?family=Parkinsans:ital,wght@0,400;0,700;1,400;1,700&family=Roboto&display=swap');
:root {
    --background-color: #203162;
    --dark-text-color: #203162;
    --text-color:#abd7ff;
    --color1: #ff9d42;
    --color2: #19a28d;
    --color3: #ffc610;
  }
  body{
    margin: 0px;
    padding: 0px;
    font-family: Roboto, Arial, Helvetica, sans-serif;
  }
  *{
    margin: 0px;
    padding: 0px;
  }
.app-content{
    padding: 4rem;
    /* background-color: var(--background-color); */
    color: var(--text-color);
    margin: 0;
}
h1,h2,h3,h4,h5,h6{
    font-family: Parkinsans, Arial, Helvetica, sans-serif;
}
.center-text{
    text-align: center;
}
a{
    text-decoration: none;
    text-decoration-color: none;
}
.linked_text a{
    background-color: var(--color1);
    color: var(--dark-text-color);
    font-weight: 600;
    padding: 0.5rem 1rem;
    border-radius: 1rem;
}
.linked_text:nth-child(3n) a{
    background-color: var(--color1);
    
}
.linked_text:nth-child(3n+1) a{
    background-color: var(--color2);
    
}
.linked_text:nth-child(3n+2) a{
    background-color: var(--color3);
    
}
.flexbox{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
}

.home{
    min-height: calc(100vh - 8rem);   
}
.home h1{
    font-size: 4rem;
}
.title{
    font-size: clamp(2rem, 10ch, 3rem);
}
@media (max-width:480px) {
    .app-content{
        padding: 10px;
    }
    .home{
        min-height: calc(100vh - 20px);   
    }
}
