.search-box.focused {
    background-color: #fff;
    box-shadow: 0 4px 8px rgba(0, 123, 255, 0.2);
}

.search-icon {
    position: absolute;
    left: 10px;
    font-size: 1.5rem;
    transition: transform 0.3s ease;
}

.search-icon.move-right {
    transform: translateX(clamp(300px, 90%, 650px));
}

.search-box {
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.3s ease;
    border: 2px solid var(--dark-text-color);
    position: relative;
    color: var(--dark-text-color);
    width: clamp(300px, 90%, 650px);
    margin: clamp(0.5rem, 5%, 1rem) 0rem;
    border-radius: 1.5rem;
    height: 2.2rem;
    padding: 0.5rem 16px;
    background-color: var(--color2);
}

.search-box.focused {
    scale: 1.1;
    box-shadow: 0 4px 8px var(--dark-text-color);
}

.search-input {
    width: 100%;
    padding-left: 30px;
    height: 1.2rem;
    font-weight: 500;
    border: none;
    outline: none;
    font-size: 1.2rem;
    background: transparent;
}

.search-input::placeholder {
    color: var(--background-color);
}